.wrapper {
  max-width: 1200px;
  margin: auto;
  padding: 0 10px;
}

.section_title {
  font-size: 32px;
  text-align: center;
  letter-spacing: 2px;
  padding-bottom: 55px;

  @media (max-width: 991px) {
    font-size: 28px;
    padding-bottom: 35px;
  }
  @media (max-width: 769px) {
    font-size: 24px;
    letter-spacing: 1px;
    padding-bottom: 35px;
  }
}




